<template>
	<view class="appointment_con">
		<view class="list">
			<!-- 预约类型 -->
			<view class="cow justify-content-between">
				<view class="left">预约类型</view>
				<view class="right flex">
					<view class="choose flex" @tap="choosePerson(0)">
						<image src="../../static/icon/gou_wxz.png" mode="" v-show="personId"></image>
						<image src="../../static/icon/gou.png" mode="" v-show="!personId"></image>
						<view>本人租铺</view>
					</view>
					<view class="choose flex" style="margin-left: 70rpx" @tap="choosePerson(1)">
						<image src="../../static/icon/gou_wxz.png" mode="" v-show="!personId"></image>
						<image src="../../static/icon/gou.png" mode="" v-show="personId"></image>
						<view>推荐他人租铺</view>
					</view>
				</view>
			</view>
			<!-- 预约时间 -->
			<view class="cow justify-content-between" :style="personId?'border-bottom: none':''">
				<view class="left">预约时间</view>
				<view class="right flex">
					<picker mode="date"  @change="getDate">
						<view class="date">{{date}}</view>
					</picker>
					<view class="heng"></view>
					<picker mode="time"  @change="getTime">
						<view class="time">{{time}}</view>
					</picker>
				</view>
			</view>
			<!-- 个人 -->
			<view v-if="!personId">
				<!-- 经营品牌 -->
				<view class="cow justify-content-between">
					<view class="left">想经营品牌</view>
					<view class="right flex">
						<input type="text" placeholder="如盒马先生；真功夫等" placeholder-style="color: #909090;" v-model="jypp"/>
					</view>
				</view>
				<view class="cow justify-content-between">
					<view class="left">经营业态</view>
					<view class="right flex">
						<picker :range="stateList" @change="getState" range-key="name" :value="stateIndex" >
							<view class="flex" style="align-items: center;">
								<view class="businessState">{{stateList[stateIndex].name}}</view>
								<image src="../../static/icon/you.png" mode="" class="jiantou"></image>
							</view>
							
						</picker>
					</view>
				</view>
				<!-- 目前经营状态 -->
				<view class="cow justify-content-between">
					<view class="left">目前经营状态</view>
					<view class="right flex">
						<view class="choose flex" @tap="chooseState(0)">
							<image src="../../static/icon/gou_wxz.png" mode="" v-show="stateId"></image>
							<image src="../../static/icon/gou.png" mode="" v-show="!stateId"></image>
							<view>首次开店</view>
						</view>
						<view class="choose flex" style="margin-left: 70rpx" @tap="chooseState(1)">
							<image src="../../static/icon/gou_wxz.png" mode="" v-show="!stateId"></image>
							<image src="../../static/icon/gou.png" mode="" v-show="stateId"></image>
							<view>有开店经验</view>
						</view>
					</view>
				</view>
				<!-- 备注 -->
				<view class="cow justify-content-between">
					<view class="left">备注</view>
					<view class="right flex">
						<input type="text" placeholder="备注" placeholder-style="color: #909090;" v-model="remark"/>
					</view>
				</view>
			</view>
			
		</view>
		<!-- 他人 -->
		<view v-if="personId">
			<view class="recommend">您推荐客户的基本情况</view>
		</view>
		<!-- 他人 -->
		<view class="list" v-if="personId">
			
			<!-- 姓名 -->
			<view class="cow justify-content-between">
				<view class="left">他的姓名</view>
				<view class="right flex">
					<input type="text" placeholder="请输入您推荐客户姓名(选填)" placeholder-style="color: #909090;" v-model="name"/>
				</view>
			</view>
			<!-- 联系方式 -->
			<view class="cow justify-content-between">
				<view class="left">他的联系方式</view>
				<view class="right flex">
					<input type="number" placeholder="请输入您推荐客户联系方式(选填)" placeholder-style="color: #909090;" maxlength="11" v-model="phone"/>
				</view>
			</view>
			<!-- 经营品牌 -->
			<view class="cow justify-content-between">
				<view class="left">想经营品牌</view>
				<view class="right flex">
					<input type="text" placeholder="如盒马先生；真功夫等" placeholder-style="color: #909090;" v-model="jypp"/>
				</view>
			</view>
			<view class="cow justify-content-between">
				<view class="left">经营业态</view>
				<view class="right flex">
					<picker :range="stateList" @change="getState" range-key="name" :value="stateIndex" >
						<view class="flex" style="align-items: center;">
							<view class="businessState">{{stateList[stateIndex].name}}</view>
							<image src="../../static/icon/you.png" mode="" class="jiantou"></image>
						</view>
						
					</picker>
				</view>
			</view>
			<!-- 目前经营状态 -->
			<view class="cow justify-content-between">
				<view class="left">目前经营状态</view>
				<view class="right flex">
					<view class="choose flex" @tap="chooseState(0)">
						<image src="../../static/icon/gou_wxz.png" mode="" v-show="stateId"></image>
						<image src="../../static/icon/gou.png" mode="" v-show="!stateId"></image>
						<view>首次开店</view>
					</view>
					<view class="choose flex" style="margin-left: 70rpx" @tap="chooseState(1)">
						<image src="../../static/icon/gou_wxz.png" mode="" v-show="!stateId"></image>
						<image src="../../static/icon/gou.png" mode="" v-show="stateId"></image>
						<view>有开店经验</view>
					</view>
				</view>
			</view>
			<!-- 来访方式 -->
			<view class="cow justify-content-between">
				<view class="left">来访方式</view>
				<view class="right flex">
					<picker :range="wayList" @change="getWay" range-key="name" :value="wayIndex" >
						<view class="flex" style="align-items: center;">
							<view class="businessState">{{wayList[wayIndex].name}}</view>
							<image src="../../static/icon/you.png" mode="" class="jiantou"></image>
						</view>
						
					</picker>
				</view>
			</view>
			<view class="cow justify-content-between">
				<view class="left">是否与客户沟通</view>
				<view class="right flex">
					<view class="choose flex" @tap="chooseLinkUp(0)">
						<image src="../../static/icon/gou_wxz.png" mode="" v-show="linkUpId"></image>
						<image src="../../static/icon/gou.png" mode="" v-show="!linkUpId"></image>
						<view>是</view>
					</view>
					<view class="choose flex" style="margin-left: 70rpx" @tap="chooseLinkUp(1)">
						<image src="../../static/icon/gou_wxz.png" mode="" v-show="!linkUpId"></image>
						<image src="../../static/icon/gou.png" mode="" v-show="linkUpId"></image>
						<view>否</view>
					</view>
				</view>
			</view>
			<!-- 备注 -->
			<view class="cow justify-content-between">
				<view class="left">备注</view>
				<view class="right flex">
					<input type="text" placeholder="备注" placeholder-style="color: #909090;" v-model="remark"/>
				</view>
			</view>
		</view>
		<view class="comfirm" @tap="comfirm">提交</view>
	</view>
</template>

<script>
	import { checkPhone } from '@/common/tools';

	export default {
		// addAppointment
		onLoad(res) {
			// this.storeId = res.storeId
			this.storeId = 1
			this.userId = uni.getStorageSync('userInfo').userId
			this.token = uni.getStorageSync('userInfo').token
			this.getWorkingTypeList()
		},
		data() {
			return {
				token: '',
				userId:'',
				// 公共
				personId: false,//true是自己 false是他人
				date: '请选择',
				time: '请选择',
				jypp:'',//经营品牌
				stateList:[],//经营业态
				stateIndex:0,//经营业态选择索引
				stateId: false,//目前经营状态
				remark:'',
				// 他人
				name: '',
				phone:'',
				wayList:[{
					name: '本人带客户上门',
					id: 0
				},{
					name: '客户独自上门',
					id: 1
				}],//来访方式
				wayIndex:0,//来访方式索引
				linkUpId: false ,//是否与客户沟通
				commitFlag: false,//提交状态控制
			};
		},
		methods:{
			getWorkingTypeList(){
				let params = {
					token: this.token
				}
				this.$http.post(this.$api.workingType,{
					data: params,
					header: {
						'content-type': 'application/json;charset=UTF-8',
						token: this.token
					}
				}).then(res=>{
					if(res.data.code == 0){
						this.stateList = []
						res.data.data.map((item,index)=>{
							this.stateList.push({
								name: item.title,
								id: index
							})
						})
						console.log(this.stateList)
					}else{
						this.toast(res.data.msg)
					}
				})
			},
			getDate(e){
				console.log(e)
				this.date = e.target.value
			},
			getTime(e){
				console.log(e)
				this.time = e.target.value
			},
			
			// 选择自己还是他人
			choosePerson(index){
				if(index == 0&&!this.personId) return 
				if(index == 1&&this.personId) return
				this.personId = !this.personId
				// 公共
				this.date = '请选择'
				this.time = '请选择'
				this.remark = ''
				this.jypp = ''
				this.stateIndex = 0
				this.stateId = false
				// 他人
				this.name = ''
				this.phone = ''
				this.wayIndex = 0
				this.linkUpId = false
			},
			// 个人
			// 经营业态
			getState(e){
				this.stateIndex = e.target.value
			},
			// 经营状态
			chooseState(index){
				if(index == 0&&!this.stateId) return
				if(index == 1&&this.stateId) return
				this.stateId = !this.stateId
			},
			// 他人
			// 经营业态
			// getState1(e){
			// 	this.stateIndex1 = e.target.value
			// 	console.log(e)
			// },
			// 经营状态
			// chooseState1(index){
			// 	if(index == 0&&!this.stateId) return
			// 	if(index == 1&&this.stateId) return
			// 	this.stateId = !this.stateId
			// },
			// 来访方式
			getWay(e){
				console.log(e)
				this.wayIndex = e.target.value
			},
			// 是否与客户沟通
			chooseLinkUp(index){
				if(index == 0&&!this.linkUpId) return
				if(index == 1&&this.linkUpId) return
				this.linkUpId = !this.linkUpId
			},
			comfirm(){
				if(this.date == "请选择"||this.time == "请选择"){
					this.toast('预约时间未选择')
					return
				}
				if(this.personId&&!this.name){
					this.toast('推荐客户姓名未填写')
					return
				}
				if (this.personId&&!checkPhone(this.phone)) return;
				if(this.personId&&!this.phone){
					this.toast('推荐客户联系方式未填写')
					return
				}
				if(!this.jypp){
					this.toast('经营品牌未填写')
					return
				}
				if(!this.remark){
					this.toast('备注未填写')
					return
				}
				if(this.commitFlag) return
				this.commitFlag = true
				let params ={
					userId: this.userId,
					type: this.personId?'推荐他人租铺':'本人租铺',
					yuTime: this.date+' '+this.time,
					nickname: this.personId?this.name:'',
					mobile: this.personId?this.phone:'',
					brand: this.jypp,
					manageType: this.stateList[this.stateIndex].name,
					
					state: !this.stateId?'首次开店':'有开店经验',
					visit: this.personId?this.wayList[this.wayIndex].name:'',
					connect: this.personId?!this.linkUpId?'是':'否':'',
					remark: this.remark,
					storeId: this.storeId
				}
				uni.showLoading({
					title: '加载中'
				});
				this.$http.post(this.$api.addAppointment,{
					data:params,
					header: {
						'content-type': 'application/json;charset=UTF-8',
						token: this.token
					}
				}).then(res=>{
					if(res.data.code == 0){
						uni.showToast({
							title: '预约成功',
							icon: 'success'
						})
						setTimeout(()=>{
							this.commitFlag = false
							uni.reLaunch({
								url:'../mineAppointment/mineAppointment'
							})
							// uni.hideLoading()
						},1000)
						
					}else{
						this.commitFlag = false
						this.toast(res.data.msg)
					}
				})
			}
		}
	}
</script>

<style lang="less" scoped>
	.appointment_con{
		width: 750rpx;
		.recommend{
			width: 750rpx;
			background: #F9F9F9;
			padding: 36rpx;
			box-sizing: border-box;
			font-size: 32rpx;
			font-family: PingFang SC;
			font-weight: 500;
			color: #343434;
		}
		.list{
			padding: 0 36rpx;
			box-sizing: border-box;
			
			.cow{
				padding: 35rpx 0;
				box-sizing: border-box;
				border-bottom: 1rpx solid #E8e8e8;
				align-items: center;
				.left{
					font-size: 28rpx;
					font-family: PingFang SC;
					font-weight: 400;
					color: #343434;
				}
				.right{
					align-items: center;
					// 有选择圆圈
					.choose{
						align-items: center;
						image{
							width: 34rpx;
							height: 34rpx;
							margin-right: 14rpx;
						}
						view{
							font-size: 26rpx;
							font-family: PingFang SC;
							font-weight: 400;
							color: #909090;
						}
					}
					// 日期和时间
					.heng{
						margin: 0 30rpx;
						width: 28rpx;
						height: 2rpx;
						background: #302E37;
					}
					.date,.time{
						font-size: 26rpx;
						font-family: PingFang SC;
						font-weight: 400;
						color: #909090;
					}
					input,.businessState{
						text-align: right;
						font-size: 26rpx;
						color: #909090;
					}
					input{
						width: 450rpx;
					}
					.jiantou{
						width: 22rpx;
						height: 22rpx;
						margin-left: 20rpx;
					}
				}
				
			}
		}
		.comfirm{
			margin: 60rpx 0 95rpx 38rpx;
			width: 674rpx;
			height: 110rpx;
			line-height: 110rpx;
			text-align: center;
			background: #302E37;
			border-radius: 12rpx;
			font-size: 34rpx;
			font-family: PingFang SC;
			font-weight: 500;
			color: #FFFFFF;
		}
	}
</style>
